.dashboard-div{
    margin-top: 1rem;
    padding-top: 4rem;
}
.dashboard-title{
    font-size: 22px;
    color: black;
    font-weight: lighter;
    font-family: Open Sans, sans-serif;
}
.dashboard-sub-title{
    font-size: 12px ;
    font-weight: lighter ;
    color: #979aaf ;
    font-family: Open Sans, sans-serif;
    margin-top: 1rem;
}

.analytics-title{
    height: auto;
    width: auto;
    display: flex;
    gap: 8px;
    align-items: center;
}

.analytics{
    width: 100%;
    height: 250px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.border-radius{
    width: 100px;
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 2px solid #ED4D6E;
    border-radius: 50%;
    padding: 15px;
}

.analytics h2{
    font-size: 100px;
    font-weight: 600;
    color: #ED4D6E;
}

.btn-holder{
    width: 100%;
    height: auto;
    display: flex;
    justify-content: center;
}

.analytics-btn{
    padding: 10px 20px;
    border: none;
    outline: none;
    background-color: #ED4D6E;
    border: 1px solid #ED4D6E;
    color: #fff;
    border-radius: 18px;
    transition: 0.5s;
}

.analytics-btn:hover{
    background-color: #fff;
    border: 1px solid #ED4D6E;
    color: #ED4D6E;
    cursor: pointer;
}