.main-header{
    max-width: 1920px;
    margin-left: auto;
    margin-right: auto;
    width: 100%;
    background-color: #FFFFFF;
    font-family: Open Sans, sans-serif;
    font-size: 0.9375rem;
    font-weight: 400;
    line-height: 1.25rem;
    box-shadow: 0 0.5rem 1rem 0 rgb(44 51 73 / 10%);
    height: 6%;
    padding: 1.25rem;
    display: flex;
    justify-content: space-between;
    position: fixed;
    z-index: 100;
}
.main-header-logo-icon{
    display: flex;
    justify-content: space-around;
}
.header-logo{
    height: 43px;
}
.hamburger{
    display: none;
}
.header-right{
    display: flex;
    align-items: center;
    padding: 0 3.25rem;
}
.profile-pic{
    height: 50px;
    width: 50px;
    border-radius: 50%;
    background-image: url("https://i.pinimg.com/originals/f8/f5/98/f8f598fb70de7de910b93b10ef7fb9e5.jpg");
    background-size: cover;
    background-repeat: no-repeat;

}
.header-profile-details{
    padding-left: 1rem;
}
.profile-name{
    font-size: 0.9375rem;
    line-height: 1.25rem;
    color: #222b45;
    font-family: Open Sans, sans-serif;
    font-weight: 400;
}
.profile-role{
    font-size: 0.8125rem;
    line-height: 1.125rem;
    color: #222b45;
    font-family: Open Sans, sans-serif;
    font-weight: 400;
}

.second-section{
    display: flex;
    height: 94%;
}
.drawer-head{
    display: none;
}
.drawer-logo{
    display: none;
}

.permanent-sidebar-main{
    width: 16%;
    background-color: #ffffff;
    box-shadow: 0 0.5rem 1rem 0 rgb(44 51 73 / 10%);
    color: #222b45;
    font-family: Open Sans, sans-serif;
    font-size: 0.9375rem;
    font-weight: 400;
    line-height: 1.25rem;
    height: 100vh;
    overflow-y: scroll;
    overflow-x: hidden;
    display: block;
    position: static;
}
.sidemain-toggle-close{
    display: none;
}
.desktop-none{
    display: none;
}

.main-page{
    background-color: #F8FAFF;
    /* margin-top: 6%; */
    padding: 1rem 2rem;
    overflow-y: scroll;
    width: 84%;
    height: 100vh;
}


.sidebar-main::-webkit-scrollbar {
    width: 3px;
    background: #e4e9f2;
  }

  .sidebar-main::-webkit-scrollbar-thumb {
    background: #e4e9f2;
    cursor: pointer;
    border-radius: 0.15625rem;
}
::-webkit-scrollbar-thumb {
    background: #b3b3b3 !important;
    border-radius: 0.063rem !important;
}
::-webkit-scrollbar {
    width: 0.188rem !important;
    height: 0.188rem !important;
}



.menu-div{
    margin-top: 6.5rem;
}

.menu-head{
    display: flex;
    border-bottom: 1px solid #edf1f7;
    align-items: center;
    padding: 0.8rem 1rem;
    cursor: pointer;
    text-decoration: none !important;
}
.menu-head:hover .menu-icon{
    color: #42a1ee !important;
}
.menu-head:hover .menu{
color: #ED4D6E !important;
}

.menu-head:active .menu-icon{
color: #ED4D6E !important;
}



.menu{
    font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande", "Lucida Sans Unicode", Geneva, Verdana, sans-serif !important;
    color: #222b45;
    font-size: 13px;
    font-weight: lighter !important;
    padding-bottom: 3px;
    padding-left: 12px;
}
.menu-act{
    font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande", "Lucida Sans Unicode", Geneva, Verdana, sans-serif !important;
    color: #ED4D6E;
    font-size: 13px;
    font-weight: lighter !important;
    padding-bottom: 3px;
    padding-left: 12px;
}
.menu-icon{
    color: #8f9bb3;
    height: 16px;
}

.menu-icon-active{
    color: #ED4D6E ;
    height: 16px;
    } 


@media only screen and (max-width: 600px) {
    .hamburger{
        display: block;
        cursor: pointer;
    }
    .header-logo{
        display: none;
    }
    /* .sidebar-main{
        position: absolute;
        z-index: 1;
    } */
    .drawer-head{
        display: flex;
        justify-content: space-between;
        padding: 0.5rem;
        align-items: center;
    }
    .drawer-logo{
        display: block;
        height: 41px;
    }
    .menu-div {
        margin-top: 1rem;
    }
    .sidemain-toggle-close{
        display: none;
    }
    /* .sidemain-toggle{
        display: block;
    } */
    .desktop-none{
        display: block;
    }
    .sidebar-main{
        display: block;
        width: 16rem;
        background-color: #ffffff;
        box-shadow: 0 0.5rem 1rem 0 rgb(44 51 73 / 10%);
        color: #222b45;
        font-family: Open Sans, sans-serif;
        font-size: 0.9375rem;
        font-weight: 400;
        line-height: 1.25rem;
        height: 100vh;
        overflow-y: scroll;
        overflow-x: hidden;
        position: absolute;
        z-index: 200;
       
    }
    .permanent-sidebar-main{
        display: none;
    }
    .main-page{
        background-color: #F8FAFF;
        /* margin-top: 6%; */
        padding: 2rem 1rem;
        overflow-y: scroll;
        width: 100%;
        height: 100vh;
    }
    
}

