.login-body{

    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #303C54;
    height: 100vh;
}
.login-form-card{
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    width: 500px;
    height: 380px;
    background-color: #F8FAFF;
    margin: 8rem auto;
    
}
.input-field{
    width: 70%;
    margin:2rem auto;
}
.form-div{
    margin-top: 3.5rem;
}
.form-title{
 margin-top: 0.5rem;
 text-align: center;
 height: 30px;
 /* font-size: 26px;
 font-family: monospace;
 font-weight: bold;
 color: lightcoral; */
}
.login-btn-div{
    display: flex;
    justify-content: center;
}
.login-btn{
    width: 70%;
    height: 40px;
    border: 1px solid #ED4D6E;
    background-color:  #ED4D6E;
    color: #FFFFFF;
    font-size: 16px;
    border-radius: 20px;
    cursor: pointer;
   font-family: auto;
   margin: 1rem auto;
}

@media only screen and (max-width: 600px) {
    .login-form-card{
  
        width: 80%;
        height: 400px;
        background-color: #F8FAFF;
        margin: 10rem auto;
        border-radius: 15px;
    }
}