.addmembers-page{
    margin-top: 1rem;
    padding-top: 4rem;
    padding-bottom: 4rem;
}
.add-title{
    font-size: 22px;
    color: black;
    font-weight: lighter;
    font-family: Open Sans, sans-serif;
}
.add-sub-title{
    font-size: 12px ;
    font-weight: lighter ;
    color: #979aaf ;
    font-family: Open Sans, sans-serif;
    margin-top: 1rem;
}
.personal-card{
    background-color: #ffffff;
    border: 0.0625rem solid #e4e9f2;
    border-radius: 0.25rem;
    box-shadow: none;
    color: #222b45;
    font-family: Open Sans, sans-serif;
    font-size: 0.9375rem;
    font-weight: 400;
    line-height: 1.25rem;
    margin-top: 2rem;
    padding: 1rem;
    
}
.card-title{
    font-size: 16px ;
    font-weight: lighter ;
    color: black;
}

.card-input-div{
    display: flex;
    margin-top: 2rem;
    justify-content: space-between;
}
.card-input{
width: 300px;
border: 1px solid #E0E0E0;
border-radius: 5px;
height: 40px;
outline: none;
}
.card-input::placeholder{
    padding-left: 1rem;
    font-size: 14px ;
    font-weight: lighter ;
    color: #666666 ;
}
.card-select-inpt-div{
    margin-top: 2rem;
}
.member-detail-title{
    font-size: 16px ;
    font-weight: lighter ;
    color: black;
}
.member-detail-desc{
    font-size: 12px ;
    font-weight: lighter ;
    color: #979aaf ;
    margin-top: 1rem;
}
.add-member-register-btn{
    width: 100%;
    height: 35px;
    border: 1px solid #ED4D6E;
    background-color:  #ED4D6E;
    color: #FFFFFF;
    font-size: 14px;
    border-radius: 4px;
    cursor: pointer;
    font-family: Open Sans, sans-serif;
    font-weight: lighter;
}
.add-member-register-btn:hover{
    border: 1px solid #ED4D6E;
    background-color:  white;
    color: #ED4D6E;
    font-weight: bold;
}
.add-member-register-disabled-btn{
    width: 100%;
    height: 35px;
    border: 1px solid #f3b7c3;
    background-color:  #f3b7c3;
    color: #FFFFFF;
    font-size: 14px;
    border-radius: 4px;
    font-family: Open Sans, sans-serif;
    font-weight: lighter;
}
.upload-text{
    font-size: 8px;
    font-family: Open Sans, sans-serif;
    color: #ED4D6E;
    margin-top: 0.5rem;
  
}
.event-select{
    margin-top: 0.5rem;
    width: 94%;
}
.add-event-btn-div{
    margin: 0 63%;
}
.add-event-btn{
    background-color: white;
    color: #EC4E6E ;
    border: 1px solid #EC4E6E;
    outline: none;
    border-radius: 5px;
    height: 43px;
    width: 162px;
    margin-top: 1rem;
    cursor: pointer;
    font-size: 16px;
    font-family: Open Sans, sans-serif;
}
.add-event-btn:hover{
    background-color: #EC4E6E;
    color: white ;
    border: 1px solid #EC4E6E;
}
.file-alert{
    font-size: 10px;
    color: #EC4E6E;
}
.event-list{
    border-left: 1px solid lightgray;
}
.image-upload{
    display: block ;
}
.mobile-image-upload{
    display: block ;
    margin-top: 1rem;
    width: 94%;
}
.date-picker{
    margin-top: 1rem;
    border: 1px solid #979aaf;
    border-radius: 5px;
    width: 90%;
    height: 36px;
    outline: none;
    padding-left: 1rem;
}
.date-picker::placeholder{
    font-size: 12px;
}
.addmember-loader{
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 40%;
    left: 50%;
}

@media only screen  and (max-width: 600px) {
    .image-upload{
        display: none ;
    }
    .mobile-image-upload{
        display: block ;
        margin-top: 1.5rem;
    }
    .add-event-btn-div{
        margin: 0;
    }
    .event-list {
        margin-top: 1rem;
    }
    .date-picker{
        width: 88%;
    }

}

@media only screen and (min-width: 601px) and (max-width: 960px) {
    .event-list {
        border-left: 1px solid lightgray;
        margin-left: 4rem;
    }
    
    .image-upload{
        display: block ;
    }
    .mobile-image-upload{
        display: block ;
    }
}

@media only screen and (min-width: 961px) and (max-width: 1200px) {
    .event-list {
        border-left: 1px solid lightgray;
        margin-left: 4rem;
    }
}

