.member-list-div{
    margin-top: 1rem;
    padding-top: 4rem;
}
.member-title{
    font-size: 22px;
    color: black;
    font-weight: lighter;
    font-family: Open Sans, sans-serif;
}
.member-sub-title{
    font-size: 12px ;
    font-weight: lighter ;
    color: #979aaf ;
    font-family: Open Sans, sans-serif;
    margin-top: 1rem;
}

#member-list table {
    border-collapse: collapse;
    border-spacing: 0;
    width: 100%;
    border: 1px solid #ddd;
    margin-top: 2rem;
    background-color: #FFFFFF;
  }
  
  th{
    text-align: left;
    padding: 8px;
    border: 1px solid #f2f2f2;
  }

  td {
    text-align: left;
    padding: 15px;
    border: 1px solid #f2f2f2;
  }

  thead {
    font-size: 16px;
    color:  #ED4D6E;
    font-weight: 200;
    font-family: Open Sans, sans-serif;
  }

  tbody{
    font-size: 14px;
    color:  #000000;
    font-weight: 200;
    font-family: Open Sans, sans-serif;
    
  }
  .member-loader{
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;
      top: 10rem;
  }
  .search-div{
    display: flex;
    justify-content: flex-end;
  }
  .search{
    width: 300px !important;
  }
  .member-search-btn{
    background-color: #FDEDF0;
    color: #EC4E6E ;
    outline: none;
    border: none;
    border-radius: 5px;
    height: 40px;
    width: 100px;
    margin-left: 20px;
    cursor: pointer;
    box-shadow: 0 4px 6px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
  }
  .member-search-btn:hover{
    background-color: #EC4E6E;
    color: white;
  }
  .member-table-div{
    overflow-x: auto;
    width: 100%;
  }


  @media only screen  and (max-width: 600px) {
    .search-div {
      margin-top: 1.5rem;
     }
    .action-box{
      width: 55px;
    }
  }

  @media only screen and (min-width: 700px) and (max-width: 810px) {
    .action-box{
      width: 55px;
    }
  }

 
