.audio-page{
    margin-top: 1rem;
    padding-top: 4rem;
}
.audio-title{
    font-size: 22px;
    color: black;
    font-weight: lighter;
    font-family: Open Sans, sans-serif;
}
.audio-sub-title{
    font-size: 12px ;
    font-weight: lighter ;
    color: #979aaf ;
    font-family: Open Sans, sans-serif;
    margin-top: 1rem;
}
.audio-card-head{
    background-color: #ffffff;
    border: 0.0625rem solid #e4e9f2;
    border-radius: 0.25rem;
    box-shadow: none;
    color: #222b45;
    font-family: Open Sans, sans-serif;
    font-size: 0.9375rem;
    font-weight: 400;
    line-height: 1.25rem;
    margin-top: 2rem;
    padding: 1rem;
    width: 100%;
}
.audio-cardlist-head{
    background-color: #ffffff;
    border: 0.0625rem solid #e4e9f2;
    border-radius: 0.25rem;
    box-shadow: none;
    color: #222b45;
    font-family: Open Sans, sans-serif;
    font-size: 0.9375rem;
    font-weight: 400;
    line-height: 1.25rem;
    margin-top: 2rem;
    padding: 1rem;
    width: 100%;
    height: 400px;
    overflow-x: hidden;
    overflow-y: auto;
}
.support-text{
    color: #7AB3F8;
    font-family: Open Sans, sans-serif;
    font-size: 14px;
    font-weight: lighter;
    margin-top: 1rem;
}
.audio-add-disable-btn{
    width: 100%;
    height: 35px;
    border: 1px solid #f3b7c3;
    background-color:  #f3b7c3;
    color: #FFFFFF;
    font-size: 14px;
    border-radius: 4px;
    font-family: Open Sans, sans-serif;
    font-weight: lighter;
    margin-top: 2rem;
}
.audio-search-div{
    margin-left: 100px;
  }
  .audio-search{
    width: 300px !important;
    height: 35px !important;
  }
.audio-search-btn{
    background-color: #FDEDF0;
    color: #EC4E6E ;
    outline: none;
    border: none;
    border-radius: 5px;
    height: 35px;
    width: 100px;
    margin-left: 20px;
    cursor: pointer;
}
.audio-search-btn:hover{
    background-color: #EC4E6E;
    color: white ;
}
.audio-list-title{
    font-size: 16px ;
    font-weight: lighter ;
    color: black;
    margin-top: 11px;
}
.audio-add-btn{
    width: 100%;
    height: 35px;
    border: 1px solid #ED4D6E;
    background-color:  #ED4D6E;
    color: #FFFFFF;
    font-size: 14px;
    border-radius: 4px;
    cursor: pointer;
    font-family: Open Sans, sans-serif;
    font-weight: lighter;
    margin-top: 2rem;
}

.audio-add-btn:hover{
    border: 1px solid #ED4D6E;
    background-color:  white;
    color: #ED4D6E;
    font-weight: bold;
}

.baseline{
    border-bottom: 1px solid lightgray;
    width: 100%;
    height: 1px;
    margin-top: 1rem;
}

.audiolist-loader{
  margin: 2rem auto;
}
.audio-table-div{
    overflow-x: auto;
}
@media only screen and (max-width: 600px) {
    .audio-card-head{
        padding: 1rem 11px;
    }
    .audio-search-div {
        margin-left: 0px;
        display: flex;
        margin-top: 2rem;
        flex-direction: column;
    }
    .audio-search-btn{
        margin: 2rem auto;
    }
}

@media only screen and (min-width: 768px) and (max-width: 819px) {

    .audio-search-div {
        margin-left: 170px;
    }
}

@media only screen and (min-width: 820px) and (max-width: 900px) {

    .audio-search-div {
        margin-left: 225px;
    }
}