.modal{

    position: fixed;
    top: 15%;
    left:25%;
    bottom: 0;
    right: 0;
    z-index: 100;
    background-color: #FFFFFF;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    border-radius: 6px;
    height: 250px;
    width: 50%;
    outline: none;
}
.view-modal{
    position: fixed;
    top: 15%;
    left:25%;
    bottom: 0;
    right: 0;
    z-index: 100;
    background-color: #FFFFFF;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    border-radius: 6px;
    height: 400px;
    width: 50%;
    outline: none;
    overflow-y: scroll;
}

.view-event-body-head{
    margin-top: 1rem;
}
.view-event-div{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.delete-event-div{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.delete-event-title{
    font-size: 24px;
    font-weight: bold;
    padding: 10px;
    font-family: Open Sans, sans-serif;

}
.view-event-title{
    font-size: 24px;
    color:  #ED4D6E;
    font-weight: bold;
    padding: 10px;
    font-family: Open Sans, sans-serif;

}
.gallery{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    padding: 10px;


}
.image-grid{
    height: 150px;
    width: 180px;
    margin-top: 1rem;

}
.file-name{
    font-size: 13px;
    font-weight: lighter;
    margin-top: 10px;
}
.view-event-body-title{
    font-size: 22px;
    font-weight: lighter;
    margin-top: 10px;
    padding: 10px;
}

.modal-delete-btn{
    background-color: #77ff99;
    color: #FFFFFF ;
    outline: none;
    border: none;
    border-radius: 5px;
    height: 40px;
    width: 120px;
    margin-left: 10px;
    cursor: pointer;
}
.modal-cancel-btn{
    background-color: #ff4b4b;
    color: #FFFFFF ;
    outline: none;
    border: none;
    border-radius: 5px;
    height: 40px;
    width: 120px;
    margin-left: 10px;
    cursor: pointer;
}
.delete-btn-div{
    margin-top: 1.3rem;
    display: flex;
    justify-content: flex-end;
    margin-right: 3rem;
}
.delete-title{
    margin: 2rem auto;
    padding: 10px;
    font-size: 20px;
    color:  #000000;
    font-weight: lighter;
}

@media only screen  and (max-width: 600px) {
    .view-modal{
        left:10px;
        height: 540px;
        width: 95%;    
    }
    .gallery {
        flex-wrap: none;
        justify-content: center;
        align-items: center;
    }
}